
<style scoped>
.bg{position: fixed;top: 0;left: 0;width: 100%;height: 100%;z-index: 99999}
.bg img{width: 100%;height: 100%;}
.content{position: absolute;top:50%;left:50%;transform: translate(-50%,-50%);background: none;}
.flexLeft {color: #fff;font-size: 0.7rem;}
.right{padding: 1rem;background:rgba(255, 255, 255,0.5);border-radius: 0.5rem;white-space: nowrap;}
.row{width: 100%;}
.pt{width: 5rem;padding-left: 0.5rem;}
.flexRight a{text-decoration: underline;display: inline-block}
.el-button {width: 100%;}
</style>>
<template>
    <div class="bg">
        <img src="../static/img/login.jpg">
        <div class="content column">
            <div class="font24 textcenter mb20">海多医疗后台管理系统</div>
            <div class="right columnLeft login" v-if="isLogin">
                <el-form ref="form" >

               <el-form-item> <div class="row"><i class="iconfont icon-yonghuming blue"></i><span class="pt">用户名：</span><el-input   placeholder="请输入用户名"></el-input></div></el-form-item>
                <el-form-item> <div class="row"><i class="iconfont icon-mima blue"></i><span  class="pt">密码：</span><el-input   placeholder="请输入密码" type="password"></el-input></div></el-form-item>
                <el-form-item> <div class="row ">
                     <el-checkbox v-model="checked">记住密码</el-checkbox>
                     <div class="flexRight blue"><a @click="change">立即注册</a></div>
                </div></el-form-item>
                <el-form-item> <el-button type="primary" @click="onSubmit(0)">登录</el-button> </el-form-item>
                </el-form>
            </div>
            <div class="right columnLeft register" v-if='!isLogin'>
                 <el-form ref="form1" >
                <el-form-item> <div class="row"><i class="iconfont icon-dianhuahaoma blue"></i><span  class="pt">手机号：</span><el-input   placeholder="请输入手机号" type="number"></el-input></div></el-form-item>
                <el-form-item> <div class="row"><i class="iconfont icon-mima blue"></i><span  class="pt">验证码:</span><el-input   placeholder="请输入手机验证码" type="number"></el-input></div></el-form-item>
                <el-form-item><div class="row"><i class="iconfont icon-yonghuming blue"></i><span class="pt">用户名：</span><el-input   placeholder="请输入用户名"></el-input></div></el-form-item>
                <el-form-item> <div class="row"><i class="iconfont icon-mima blue"></i><span  class="pt">密码：</span><el-input   placeholder="请输入密码" type="password"></el-input></div></el-form-item>
                <el-form-item> <div class="row ">
                     <div class="flexRight blue"><a @click="change">已有账号？</a></div>
                </div></el-form-item>
               <el-form-item> <el-button type="primary"@click="onSubmit(1)" >立即注册</el-button></el-form-item>
                 </el-form>
            </div>
        </div>
    </div>
</template>>
<script>
 import {mapGetters} from 'vuex'
 
  export default {
    data() {
      return {
        isLogin:true,
        checked:false
       
      }
    },
    methods: {
        change(){
            let that=this;
            this.isLogin=!that.isLogin;
        },
        onSubmit(index){
            this.$router.push({
                name:"index"
            })
        }
    },
   
    created() {
      
    },
    computed: {
      ...mapGetters({
       
      }),
    },
    
  }
</script>


